exports.push([module.id, "\ndiv#container {\n  position: relative;\n  max-width: 860px;\n  margin-left: auto;\n  margin-right: auto;\n  font-family: 'AvenirInterVarsity', 'Futura', 'Century Gothic', Helvetica, Arial, sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: #2c3e50;\n  margin-top: 60px;\n}\ndiv#container h4 {\n  margin: 0;\n  margin-top: 40px;\n}\ndiv#container input, div#container label {\n  display: block;\n  text-align: left;\n}\ndiv#container label {\n  margin-top: 5px;\n}\ndiv#container input {\n  margin-bottom: 10px;\n  padding: 3px 0;\n}\nbutton#multiline-on {\n  border: 1px solid #888;\n  padding: 3px 5px;\n  float: right;\n  background-image: none;\n  background: #c8e6c9;\n}\nbutton#multiline-on.active {\n  background-image: none;\n  background: #ffcdd2;\n}\ndiv#container #multiline-container {\n  display: none;\n}\ndiv#container #multiline-container.enabled {\n  display: block;\n}\n#school-name-field .error-message {\n  display: none;\n}\n#school-name-field.error .error-message {\n  display: inline;\n  color: red;\n}\n#school-name-field.error {\n  background: #fee;\n}\n.valid {\n  color: green;\n}\n#actions {\n  margin: 20px;\n  margin-left: auto;\n  margin-right: auto;\n  text-align: center;\n}\n#actions .btn {\n  text-decoration: none;\n  color: #2c3e50;\n  border: 1px solid #ccc;\n  padding: 10px;\n  margin: 2px;\n}\n#actions .btn:hover {\n  color: white;\n  background: #006680;\n  -moz-box-shadow:    1px 1px 1px 1px #ddd;\n  -webkit-box-shadow: 1px 1px 1px 1px #ddd;\n  box-shadow:         1px 1px 1px 1px #ddd;\n}\n.cnv-logo {\n  border: 1px solid #eee;\n  margin: 0;\n  margin-top: 5px;\n  -moz-box-shadow:    3px 3px 3px 3px #ddd;\n  -webkit-box-shadow: 3px 3px 3px 3px #ddd;\n  box-shadow:         3px 3px 3px 3px #ddd;\n}\n.cnv-logo-large {\n  display: none;\n}\n.image-holder {\n  display: none;\n}\nhr {\n  margin-top: 50px;\n  border: 0;\n  height: 1px;\n  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));\n}\n#previews-container #preview-info {\n  display: none;\n}\n#previews-container.hidden #preview-info {\n  margin-top: 30px;\n  text-align: center;\n  display: block;\n}\n#previews-container.hidden #previews {\n  display: none;\n}\n.two-columns {\n  width: 100%\n}\n.two-columns div {\n  width: 420px;\n}\n.two-columns div div {\n  display: block\n}\n#default-logos {\n  float: left;\n}\n#inverse-logos {\n  float: right;\n}\n\n", "", {"version":3,"sources":["/Users/andrew/ivcf/logo-generator/src/components/src/components/LogoGen.vue"],"names":[],"mappings":";AAsUA;EACA,mBAAA;EACA,iBAAA;EACA,kBAAA;EACA,mBAAA;EACA,4FAAA;EACA,oCAAA;EACA,mCAAA;EACA,eAAA;EACA,iBAAA;CACA;AAEA;EACA,UAAA;EACA,iBAAA;CACA;AAEA;EACA,eAAA;EACA,iBAAA;CACA;AAEA;EACA,gBAAA;CACA;AAEA;EACA,oBAAA;EACA,eAAA;CACA;AAEA;EACA,uBAAA;EACA,iBAAA;EACA,aAAA;EACA,uBAAA;EACA,oBAAA;CACA;AAEA;EACA,uBAAA;EACA,oBAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,eAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,gBAAA;EACA,WAAA;CACA;AAEA;EACA,iBAAA;CACA;AAEA;EACA,aAAA;CACA;AAEA;EACA,aAAA;EACA,kBAAA;EACA,mBAAA;EACA,mBAAA;CACA;AAEA;EACA,sBAAA;EACA,eAAA;EACA,uBAAA;EACA,cAAA;EACA,YAAA;CACA;AAEA;EACA,aAAA;EACA,oBAAA;EACA,yCAAA;EACA,yCAAA;EACA,yCAAA;CACA;AAEA;EACA,uBAAA;EACA,UAAA;EACA,gBAAA;EACA,yCAAA;EACA,yCAAA;EACA,yCAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,iBAAA;EACA,UAAA;EACA,YAAA;EACA,qGAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,iBAAA;EACA,mBAAA;EACA,eAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,WAAA;CACA;AAEA;EACA,aAAA;CACA;AAEA;EACA,cAAA;CACA;AAEA;EACA,YAAA;CACA;AAEA;EACA,aAAA;CACA","file":"LogoGen.vue","sourcesContent":["<template>\n  <div id=\"container\">\n    <help-box></help-box>\n    <img class=\"image-holder\" id=\"image-holder-wide\" src='../assets/image/WideNormal.svg'>\n    <img class=\"image-holder\" id=\"image-holder-wide-inverse\" src='../assets/image/WideDarkBg.svg'>\n    <img class=\"image-holder\" id=\"image-holder-square\" src='../assets/image/SquareNormal.svg'>\n    <img class=\"image-holder\" id=\"image-holder-square-inverse\" src='../assets/image/SquareNormalDarkBg.svg'>\n    <h4>Fill out the form below; logos using your inputs will be automatically generated for you.</h4>\n\n    <label id=\"name-label\" for=\"txt-school\">Your Name (required)</label>\n    <input\n      id=\"txt-name\"\n      placeholder=\"Your Name (required)\"\n      style=\"width:400px\"\n      tabindex=\"1\"\n      type=\"text\"\n      v-focus\n      v-on:keyup=\"validateName()\"\n      v-model=\"userName\" />\n\n    <label id=\"email-label\" for=\"txt-email\">Email Address (required)</label>\n    <input\n      id=\"txt-email\"\n      placeholder=\"name@intervarsity.org\"\n      style=\"width:400px\"\n      tabindex=\"2\"\n      type=\"text\"\n      v-on:keyup=\"validateEmail()\"\n      v-model=\"userEmail\" />\n\n    <div id=\"school-name-field\">\n      <button\n        id=\"multiline-on\"\n        name=\"multiline-on\"\n        tabindex=\"5\"\n        type=\"checkbox\"\n        v-on:click=\"toggleMultiline()\">\n        Add Second Text Line\n      </button>\n\n      <label for=\"txt-school\">School/Area/Region Full Name (e.g. University of College)</label>\n      <input\n        id=\"txt-school\"\n        placeholder=\"School Name\"\n        style=\"width:400px\"\n        tabindex=\"3\"\n        type=\"text\"\n        v-model=\"schoolName\"\n        v-on:keyup=\"redrawText()\" />\n\n      <div id=\"multiline-container\">\n        <label for=\"txt-line-two\">Second Text Line</label>\n        <input\n          id=\"txt-line-two\"\n          style=\"width:400px\"\n          tabindex=\"6\"\n          type=\"text\"\n          v-model=\"secondLine\"\n          v-on:keyup=\"redrawText()\" />\n      </div>\n      <span class=\"error-message\">Sorry, that name is too long.</span>\n    </div>\n    <label for=\"txt-school-short\">Abbreviated Name (e.g. UofC)</label>\n    <input\n      id=\"txt-school-short\"\n      maxlength=\"10\"\n      placeholder=\"Short Name\"\n      style=\"width:200px\"\n      tabindex=\"4\"\n      type=\"text\"\n      v-model=\"shortName\"\n      v-on:keyup=\"redrawText()\" />\n\n    <div id=\"actions\">\n      <a class=\"btn btn-primary\"\n         href=\"#\"\n         id=\"btn-download\"\n         tabindex=\"7\"\n         v-on:click=\"downloadLogos($event)\"\n         v-show=\"showButtons\">\n        Download ZIP\n      </a>\n      <a class=\"btn btn-secondary\"\n         href=\"#\"\n         id=\"brn-reload\"\n         tabindex=\"8\"\n         v-on:click=\"resetPage()\"\n         v-show=\"showButtons\">\n        Reset Page\n      </a>\n    </div>\n\n    <hr>\n\n    <div id=\"previews-container\" class=\"hidden\">\n      <span id=\"preview-info\">Previews will show once you start entering a name!</span>\n      <div id=\"previews\">\n        <h1>Logo Previews</h1>\n\n        <div class=\"two-columns\">\n          <div id=\"default-logos\">\n            <h2>Standard Logos</h2>\n            <wide-logo-form :config=\"wide\" :schoolName=\"schoolName\" :secondLine=\"secondLine\" :multiline=\"multilineOn\"></wide-logo-form>\n            <wide-logo-short :config=\"wide\" :shortName=\"shortName\"></wide-logo-short>\n            <square-logo-form :config=\"square\" :shortName=\"shortName\"></square-logo-form>\n          </div>\n          <div id=\"inverse-logos\">\n            <h2>Logos for Dark Backgrounds</h2>\n            <wide-logo-inverse :config=\"wide\" :schoolName=\"schoolName\" :secondLine=\"secondLine\" :multiline=\"multilineOn\"></wide-logo-inverse>\n            <wide-short-inverse :config=\"wide\" :shortName=\"shortName\" :secondLine=\"secondLine\"></wide-short-inverse>\n            <square-logo-inverse :config=\"square\" :shortName=\"shortName\"></square-logo-inverse>\n            <p>Note: the color you see here is just for demo purposes; the logos you download will have a transparent background.</p>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n// Components\nimport Vue from 'vue'\nimport SquareLogoForm from '@/components/SquareLogoForm'\nimport SquareLogoInverse from '@/components/SquareLogoInverse'\nimport WideLogoForm from '@/components/WideLogoForm'\nimport WideLogoInverse from '@/components/WideLogoInverse'\nimport WideLogoShortName from '@/components/WideLogoShortName'\nimport WideLogoShortInverse from '@/components/WideLogoShortInverse'\nimport HelpBox from '@/components/HelpBox'\n\nimport avenir from '@/avenir'\nimport wideSvg from '@/wide_svg'\nimport wideSvgInverse from '@/wide_svg_inverse'\nimport squareSvg from '@/square_svg'\nimport squareSvgInverse from '@/square_svg_inverse'\n\n// Utils\nimport ajax from 'vuejs-ajax'\nimport JSZip from 'jszip'\nimport { saveAs } from 'file-saver/FileSaver'\nconst uuidv4 = require('uuid/v4')\n\nVue.use(ajax)\n\nexport default {\n  name: 'LogoGen',\n  components: {\n    'wide-logo-form': WideLogoForm,\n    'wide-logo-inverse': WideLogoInverse,\n    'wide-logo-short': WideLogoShortName,\n    'wide-short-inverse': WideLogoShortInverse,\n    'square-logo-inverse': SquareLogoInverse,\n    'square-logo-form': SquareLogoForm,\n    'help-box': HelpBox\n  },\n  data () {\n    return {\n      schoolName: 'Your School Name Here',\n      shortName: 'UofC',\n      userName: '',\n      userEmail: '',\n      showButtons: true,\n      multilineOn: false,\n      secondLine: '',\n      wide: {\n        canvasHeight: 120,\n        canvasWidth: 400,\n        fontMax: 26,\n        fontMin: 20,\n        fontCurSize: 26,\n        lineOneDrop: 108,\n        lineTwoCanvasHeight: 140,\n        lineTwoDrop: 135,\n        lineTwoFontSize: 21,\n        logoHeight: 72,\n        logoStartX: 10,\n        logoStartY: 5,\n        logoWidth: 380,\n        rightOffset: 390,\n        svgRaw: wideSvg,\n        svgRawInverse: wideSvgInverse,\n        textDrop: 110,\n        textOffset: 10,\n        large: {\n          canvasHeight: 375,\n          canvasWidth: 1250,\n          fontMax: 81,\n          fontMin: 63,\n          lineOneDrop: 338,\n          lineTwoCanvasHeight: 438,\n          lineTwoDrop: 422,\n          lineTwoFontSize: 66,\n          logoHeight: 225,\n          logoStartX: 31,\n          logoStartY: 16,\n          logoWidth: 1188,\n          rightOffset: 1219,\n          textDrop: 344,\n          textOffset: 31\n        }\n      },\n      square: {\n        canvasHeight: 250,\n        canvasWidth: 250,\n        fontSize: 20,\n        logoHeight: 200,\n        logoStartX: 35,\n        logoStartY: 10,\n        logoWidth: 180,\n        svgRaw: squareSvg,\n        svgRawInverse: squareSvgInverse,\n        textDrop: 235,\n        textOffset: 125,\n        large: {\n          canvasHeight: 781,\n          canvasWidth: 781,\n          fontSize: 63,\n          logoHeight: 625,\n          logoStartX: 109,\n          logoStartY: 31,\n          logoWidth: 563,\n          textDrop: 734,\n          textOffset: 391\n        }\n      }\n    }\n  },\n  methods: {\n    toggleMultiline () {\n      this.multilineOn = !this.multilineOn\n      document.getElementById('multiline-container').classList.toggle('enabled')\n      if (this.multilineOn) {\n        document.getElementById('txt-line-two').focus()\n        document.getElementById('multiline-on').textContent = 'Remove Second Text Line'\n      } else {\n        document.getElementById('multiline-on').textContent = 'Add Second Text Line'\n      }\n      document.getElementById('multiline-on').classList.toggle('active')\n      this.redrawText()\n    },\n    redrawText () {\n      this.showButtons = true\n      document.getElementById('previews-container').classList.remove('hidden')\n\n      for (let child of this.$children) {\n        child.redrawText()\n      }\n    },\n    downloadLogos (e) {\n      e.preventDefault()\n      if (document.getElementsByClassName('valid').length < 2) {\n        alert('Please provide your name and email address!')\n        return\n      }\n      this.redrawText()\n      var zip = new JSZip()\n\n      // Record data about the submission in json\n      var timestamp = new Date()\n      var submissionInfo = {\n        name: this.userName,\n        email: this.userEmail,\n        schoolName: this.schoolName,\n        secondLine: this.secondLine,\n        shortName: this.shortName,\n        timestamp: timestamp.toJSON(),\n        uuid: uuidv4()\n      }\n      zip.file('submission.json', JSON.stringify(submissionInfo, null, 2))\n\n      for (let child of this.$children) {\n        if (child.fileName) {\n          zip.file(child.fileName, child.imageData, { base64: true })\n        }\n\n        if (child.fileNameLarge) {\n          zip.file(child.fileNameLarge, child.imageDataLarge, { base64: true })\n        }\n\n        if (child.svgName) {\n          var fullSvg = child.svgData.replace(/<defs\\/>/, avenir)\n          zip.file(child.svgName, fullSvg)\n        }\n      }\n      zip.generateAsync({ type: 'base64' }).then(function (content) {\n        var url = 'https://rta8nroxoc.execute-api.us-east-1.amazonaws.com/default/FileUpload?uuid=' + submissionInfo.uuid\n        var oReq = new XMLHttpRequest()\n        oReq.open('POST', url, true)\n        oReq.send(content)\n      })\n\n      zip.generateAsync({ type: 'blob' }).then(function (content) {\n        saveAs(content, 'intervarsity-logos.zip')\n      })\n    },\n    resetPage () {\n      this.userName = ''\n      this.userEmail = ''\n      this.schoolName = ''\n      this.shortName = ''\n      this.redrawText()\n      var txt = document.getElementById('txt-school')\n      txt.focus()\n    },\n    validateName () {\n      if (this.userName) {\n        document.getElementById('name-label').classList.add('valid')\n      } else {\n        document.getElementById('name-label').classList.remove('valid')\n      }\n    },\n    validateEmail () {\n      var regex = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|\"(?:[\\x01-\\x08\\x0b\\x0c\\x0e-\\x1f\\x21\\x23-\\x5b\\x5d-\\x7f]|\\\\[\\x01-\\x09\\x0b\\x0c\\x0e-\\x7f])*\")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\\x01-\\x08\\x0b\\x0c\\x0e-\\x1f\\x21-\\x5a\\x53-\\x7f]|\\\\[\\x01-\\x09\\x0b\\x0c\\x0e-\\x7f])+)\\])/\n\n      var result = regex.exec(this.userEmail)\n      if (result) {\n        document.getElementById('email-label').classList.add('valid')\n      } else {\n        document.getElementById('email-label').classList.remove('valid')\n      }\n    }\n  }\n}\n</script>\n\n<style>\ndiv#container {\n  position: relative;\n  max-width: 860px;\n  margin-left: auto;\n  margin-right: auto;\n  font-family: 'AvenirInterVarsity', 'Futura', 'Century Gothic', Helvetica, Arial, sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: #2c3e50;\n  margin-top: 60px;\n}\n\ndiv#container h4 {\n  margin: 0;\n  margin-top: 40px;\n}\n\ndiv#container input, div#container label {\n  display: block;\n  text-align: left;\n}\n\ndiv#container label {\n  margin-top: 5px;\n}\n\ndiv#container input {\n  margin-bottom: 10px;\n  padding: 3px 0;\n}\n\nbutton#multiline-on {\n  border: 1px solid #888;\n  padding: 3px 5px;\n  float: right;\n  background-image: none;\n  background: #c8e6c9;\n}\n\nbutton#multiline-on.active {\n  background-image: none;\n  background: #ffcdd2;\n}\n\ndiv#container #multiline-container {\n  display: none;\n}\n\ndiv#container #multiline-container.enabled {\n  display: block;\n}\n\n#school-name-field .error-message {\n  display: none;\n}\n\n#school-name-field.error .error-message {\n  display: inline;\n  color: red;\n}\n\n#school-name-field.error {\n  background: #fee;\n}\n\n.valid {\n  color: green;\n}\n\n#actions {\n  margin: 20px;\n  margin-left: auto;\n  margin-right: auto;\n  text-align: center;\n}\n\n#actions .btn {\n  text-decoration: none;\n  color: #2c3e50;\n  border: 1px solid #ccc;\n  padding: 10px;\n  margin: 2px;\n}\n\n#actions .btn:hover {\n  color: white;\n  background: #006680;\n  -moz-box-shadow:    1px 1px 1px 1px #ddd;\n  -webkit-box-shadow: 1px 1px 1px 1px #ddd;\n  box-shadow:         1px 1px 1px 1px #ddd;\n}\n\n.cnv-logo {\n  border: 1px solid #eee;\n  margin: 0;\n  margin-top: 5px;\n  -moz-box-shadow:    3px 3px 3px 3px #ddd;\n  -webkit-box-shadow: 3px 3px 3px 3px #ddd;\n  box-shadow:         3px 3px 3px 3px #ddd;\n}\n\n.cnv-logo-large {\n  display: none;\n}\n\n.image-holder {\n  display: none;\n}\n\nhr {\n  margin-top: 50px;\n  border: 0;\n  height: 1px;\n  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));\n}\n\n#previews-container #preview-info {\n  display: none;\n}\n\n#previews-container.hidden #preview-info {\n  margin-top: 30px;\n  text-align: center;\n  display: block;\n}\n\n#previews-container.hidden #previews {\n  display: none;\n}\n\n.two-columns {\n  width: 100%\n}\n\n.two-columns div {\n  width: 420px;\n}\n\n.two-columns div div {\n  display: block\n}\n\n#default-logos {\n  float: left;\n}\n\n#inverse-logos {\n  float: right;\n}\n\n</style>\n"],"sourceRoot":""}]);

